/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { memo, createInterpolateElement } from '@wordpress/element';
import { Button } from '@wordpress/components';

/**
 * ArkbColorPicker
 */
export default memo(({ className, colors, value, onClick, helpLink }) => {
	return (
		<>
			<fieldset className={`arkb-colorPicker ${className}`}>
				{colors.map((color) => {
					const isSelected = value === color.value;
					return (
						<Button
							key={`btn_${color.value}`}
							className={`arkb-colorPicker__btn ${color.btnClass || ''}`}
							title={color.label}
							data-color={color.value}
							data-selected={isSelected}
							onClick={() => {
								if (isSelected) {
									onClick('');
								} else {
									onClick(color.value);
								}
							}}
						>
							<span
								className='__track'
								style={{
									// 直接カラーを指定する場合。
									'--arkb--the-color--bg': color?.bg,
									'--arkb--the-color--border': color?.border,
								}}
							>
								<svg
									className='__svg'
									xmlns='http://www.w3.org/2000/svg'
									viewBox='0 0 24 24'
									width='24'
									height='24'
									aria-hidden='true'
									focusable='false'
								>
									<path d='M16.7 7.1l-6.3 8.5-3.3-2.5-.9 1.2 4.5 3.4L17.9 8z'></path>
								</svg>
							</span>
						</Button>
					);
				})}
			</fieldset>
			{helpLink && (
				<div className='arkb-helpText'>
					{createInterpolateElement(
						__('You can set the colors in the <a>Settings page</a>.'),
						{
							a: (
								// eslint-disable-next-line jsx-a11y/anchor-has-content
								<a href={helpLink} target='_blank' rel='noopener noreferrer' />
							),
						}
					)}
				</div>
			)}
		</>
	);
});
