/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { createBlock, registerBlockType } from '@wordpress/blocks';
import {
	BlockControls,
	RichText,
	InnerBlocks,
	InspectorControls,
	useBlockProps,
} from '@wordpress/block-editor';
import { useCallback } from '@wordpress/element';
import { PanelBody, ButtonGroup, Button, BaseControl } from '@wordpress/components';
import { useInnerBlocksProps } from '@compatible';

/**
 * @Internal dependencies
 */
import metadata from './block.json';
import deprecated from './deprecated';
import blockIcon from './icon';
import { ArkbColorPicker, LsIcon, LsIconPicker, ArkbMarginControl, ArkbProOnly } from '@components';

const IS_PRO = !!window.arkbSettings?.pfkey || false;

/**
 * 設定
 */
const NOTICE_TYPES = [
	{
		value: 'point',
		icon: 'LsLightbulb',
	},
	{
		value: 'alert',
		icon: 'LsAlert',
	},
	{
		value: 'warning',
		icon: 'LsWarning',
	},
	{
		value: 'ok',
		icon: 'LsCheck',
	},
	{
		value: 'memo',
		icon: 'LsPen',
	},
];

const NOTICE_COLORS = NOTICE_TYPES.map((item) => {
	return {
		value: item.value,
		btnClass: `ark-block-notice -${item.value}`,
		bg: 'var(--arkb-notice_bg)',
		border: 'var(--arkb-notice_border)',
	};
});

/**
 * 通知ブロック
 */
const blockName = 'ark-block-notice';
registerBlockType(metadata.name, {
	title: __('Notice', 'arkhe-blocks'),
	description: __('Create content that is prominently emphasized.', 'arkhe-blocks'),
	icon: blockIcon,
	styles: [
		{ name: 'default', label: __('Default', 'arkhe-blocks'), isDefault: true },
		{ name: 'stronger', label: __('Stronger', 'arkhe-blocks') },
		{ name: 'simple', label: __('Simple', 'arkhe-blocks') },
	],
	transforms: {
		from: [
			//どのブロックタイプから変更できるようにするか
			{
				type: 'block',
				blocks: ['core/paragraph'],
				transform: (attributes) => {
					const innerP = createBlock('core/paragraph', {
						content: attributes.content,
					});
					return createBlock(metadata.name, { className: 'is-style-simple' }, [innerP]);
				},
			},
		],
	},
	edit: ({ attributes, setAttributes }) => {
		const { type, icon, title } = attributes;

		let blockClass = blockName;
		if (type) {
			blockClass = `${blockClass} -${type}`;
		}

		// アイコン選択時の処理
		const setIcon = useCallback(
			(val) => {
				if (!IS_PRO) return;
				setAttributes({ icon: val });
			},
			[setAttributes]
		);

		const setColor = useCallback(
			(val) => {
				setAttributes({ type: val || 'point' });
			},
			[setAttributes]
		);

		// Props
		const blockProps = useBlockProps({
			className: blockClass,
		});
		const innerBlocksProps = useInnerBlocksProps(
			{
				className: `${blockName}__body ark-keep-mt--s`,
			},
			{
				template: [['core/paragraph']],
				templateLock: false,
			}
		);

		return (
			<>
				<BlockControls>
					<ArkbMarginControl {...{ className: attributes.className, setAttributes }} />
				</BlockControls>
				<InspectorControls>
					<PanelBody title={__('Presets', 'arkhe-blocks')} initialOpen={true}>
						<ButtonGroup className='arkb-btns--noticePresets' help='aaa'>
							{NOTICE_TYPES.map((data) => {
								return (
									<Button
										key={`ark-${data.value}`}
										className={`ark-block-notice -${data.value}`}
										onClick={() => {
											setAttributes({ type: data.value, icon: data.icon });
										}}
									>
										<LsIcon icon={data.icon} size='20' />
									</Button>
								);
							})}
						</ButtonGroup>
						<div className='arkb-helpText u-arkb--mt--10'>
							{__('You can change the icon and color at once.', 'arkhe-blocks')}
						</div>
					</PanelBody>
					<PanelBody title={__('Icon settings', 'arkhe-blocks')} initialOpen={true}>
						<BaseControl>
							<BaseControl.VisualLabel>
								{__('Color', 'arkhe-blocks')}
							</BaseControl.VisualLabel>
							<ArkbColorPicker
								className='-noticeBlock'
								value={type || 'point'}
								colors={NOTICE_COLORS}
								onClick={setColor}
								// helpLink={addQueryArgs('admin.php', {
								// 	page: 'arkhe_blocks_settings',
								// 	tab: 'block',
								// })}
							/>
						</BaseControl>
						<ArkbProOnly>
							<BaseControl>
								<BaseControl.VisualLabel>
									{__('Select Icon', 'arkhe-blocks')}
								</BaseControl.VisualLabel>
								<LsIconPicker value={icon} onChange={setIcon} clearable={false} />
							</BaseControl>
						</ArkbProOnly>
					</PanelBody>
				</InspectorControls>
				<div {...blockProps}>
					<div className={`${blockName}__head`}>
						<LsIcon icon={icon} className={`${blockName}__icon`} size='20' />
						<RichText
							tagName='span'
							className={`${blockName}__title`}
							placeholder={__('Enter text', 'arkhe-blocks') + '...'}
							value={title}
							onChange={(newTitle) => setAttributes({ title: newTitle })}
						/>
					</div>
					<div {...innerBlocksProps} />
				</div>
			</>
		);
	},

	save: ({ attributes }) => {
		const { icon, title, type } = attributes;

		let blockClass = blockName;
		if (type) {
			blockClass = `${blockClass} -${type}`;
		}

		const blockProps = useBlockProps.save({
			className: blockClass,
		});

		const attrClass = attributes.className || '';
		const isSimpleStyle = -1 !== attrClass.indexOf('is-style-simple');

		return (
			<div {...blockProps}>
				<div className={`${blockName}__head`}>
					<LsIcon icon={icon} className={`${blockName}__icon`} size='20' />
					{!isSimpleStyle && (
						<RichText.Content
							tagName='span'
							className={`${blockName}__title`}
							value={title}
						/>
					)}
				</div>
				<div className={`${blockName}__body ark-keep-mt--s`}>
					<InnerBlocks.Content />
				</div>
			</div>
		);
	},
	deprecated,
});
