/**
 * @WordPress dependencies
 */
import { RawHTML } from '@wordpress/element';
import { RichText, InnerBlocks, useBlockProps } from '@wordpress/block-editor';

/**
 * @Internal dependencies
 */
import { LsIcon } from '@components';
import { migrateIconName } from '@helper/migrate';

/**
 * 通知ブロック
 */
const blockName = 'ark-block-notice';
export default [
	// 2.20でのアイコンリニューアルより前
	{
		supports: {
			anchor: true,
			className: false,
		},
		attributes: {
			title: {
				type: 'string',
				source: 'html',
				selector: '.ark-block-notice__title',
				default: '',
			},
			type: {
				type: 'string',
				default: 'point',
			},
			icon: {
				type: 'string',
				default: 'arkb-svg-point',
			},
			// 本来ないが、2.20 以前のアイコンデータを引き継ぐために追加
			iconV1: {
				type: 'string',
				source: 'html',
				selector: '.ark-block-notice__head',
				default: '',
			},
		},
		migrate(attributes) {
			const newAttrs = {};
			if (attributes.icon) {
				newAttrs.icon = migrateIconName(attributes.icon);
				console.log('Notice icon migrated:', newAttrs.icon);
			}

			return {
				...attributes,
				...newAttrs,
			};
		},
		save: ({ attributes }) => {
			const { icon, title, type, iconV1 } = attributes;

			let oldSvg = iconV1 || '';
			oldSvg = oldSvg.replace(/<span.*?<\/span>/g, ''); // arkb の span にはクラスがあることに注意

			let theIcon = null;
			if (oldSvg) {
				// console.log('Notice old icon:', oldSvg);
				theIcon = <RawHTML>{oldSvg}</RawHTML>;
			} else if (icon) {
				theIcon = <LsIcon icon={icon} className={`${blockName}__icon`} size='20' />;
			}

			let blockClass = blockName;
			if (type) {
				blockClass = `${blockClass} -${type}`;
			}

			const blockProps = useBlockProps.save({
				className: blockClass,
			});

			const attrClass = attributes.className || '';
			const isSimpleStyle = -1 !== attrClass.indexOf('is-style-simple');

			return (
				<div {...blockProps}>
					<div className={`${blockName}__head`}>
						{theIcon}
						{!isSimpleStyle && (
							<RichText.Content
								tagName='span'
								className={`${blockName}__title`}
								value={title}
							/>
						)}
					</div>
					<div className={`${blockName}__body ark-keep-mt--s`}>
						<InnerBlocks.Content />
					</div>
				</div>
			);
		},
	},
];
